.pdCont {
    display: flex;
    justify-content: center;
    padding: 20px;
  }
  
  .product-section {
    display: flex;
  }
  
  .image-container {
    flex: 1;
    margin-right: 20px;
  }
  
  .main-image {
    width: 100%;
    height: auto;
  }
  
  .carousel-container {
    flex: 1;
  }
  
  .carousel {
    display: flex;
    flex-wrap: wrap;
  }
  
  .carousel-image {
    width: 100px;
    height: 100px;
    object-fit: cover;
    margin-right: 10px;
    margin-bottom: 10px;
  }
  
  .description-container {
    flex: 1;
  }
  
  .product-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .product-description {
    font-size: 16px;
  }